.works{
    padding: 70px 0 60px;
    text-align: center;
    font-family: "Loto";
    p{
        font-size: 14px;
        color: @font1;
        font-weight: 100;
    }
    .navs{
        display: flex;
        justify-content: center;
        margin: 80px 0;
        flex-wrap: wrap;
    }
    .nav-item{
        padding: 8px 20px;
        margin: 0 10px;
        .nav-link{
            font-weight: 900;
            font-size: 14px;
            color: @font1;
            transition: 500ms;
        }
    }
    .nav-active{
        background: @blue;
        border-radius: 5px;
        .nav-link{
            color: @white;
        }
    }
    .tab-item{
        cursor: pointer;
        position: relative;
        &:hover .overlay{
            opacity: 1;
        }
        img{
            width: 100%;
            height: 100%;
        }
        .overlay{
            position: absolute;
            transition: 500ms;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(76,118,255, .8);
            display: flex;
            justify-content: center;
            align-items: center;
            opacity: 0;
            .fa {
                color: @white;
                font-size: 20px;
                border: 1px solid @white;
                border-radius: 50%;
                padding: 6px;
            }
        }
    }
}