.contact{
      padding: 80px 0 90px;
      font-family: 'Lato';
      p{
          font-size: 18px;
          font-weight: 400;
          text-align: center;
          margin-bottom: 75px;
      }
      .text-in{
          .md-block({
              text-align: center;
          });
          h5{
              font-size: 18px;
              color: @font1;
              margin-bottom: 30px;
              line-height: 23px;
          }
          p{
              font-size: 10px;
              line-height: 20px;
              color: #adb2b7;
              text-align: left;
              .md-block({
                text-align: center;
            });
          }
      }
      .text{
          margin-bottom: 50px;
      } 
      .fro{
        .md-block({
            text-align: center;
        });
          h6{
              font-size: 20px;
              color: #062033;
              margin-bottom: 45px;
              font-weight: 900;
          }
      }
      .form-control{
          width: 100%;
          margin-bottom: 25px;
          border: none;
          border-bottom: 1px solid #e0e0e0;
          padding-bottom: 10px;
          &:focus{
              border: 1px solid @blue;
              box-shadow: 0 0 4px @blue;
          }
      }
      .social{
          display: flex;
          flex-direction: column;
          align-items: flex-end;
          .md-block({
             width: 100%;
             display: inline-block; 
             justify-content: center;
             align-items: center;
             text-align: center;
          
             
          });
          .fa{
              color: #cdd2d6;
              font-size: 22px;
              line-height: 34px;
              .md-block({
                  margin: 10px 25px;
              })
          }
      }
      .send-meseg{
          display: flex;
          justify-content: flex-end;
          cursor: pointer;
          p{
              font-size: 13px;
          }
          .fa{
              margin-right: 11px;
              color: @blue;
              font-size: 15px;
          }
      }
      
}