img{
    max-width: 100%;
    height: auto;
}
body{
    min-width: 320px;
    font-family: 'Lato';
}
h3{
    font-weight: 900;
    font-size: 28px;
    color: @blue;
    margin-bottom: 15px;
    text-align: center;
    text-transform: uppercase;
}
a{
    text-decoration: none;
}
.active-link{
    color: @blue !important;
    flex-wrap: 700;
}
.heart{
    max-width: 230px;
    margin: auto;
    text-align: center;
    margin-bottom: 50px;
    &::before{
        content: "";
        display: inline-block;
        width: 90px;
        height: 2px;
        background: #d7dadc;
        transform: translate(-15px, -7px);
    }
    &::after{
        content: "";
        display: inline-block;
        width: 90px;
        height: 2px;
        background: #d7dadc;
        transform: translate(15px, -7px);
    }
}


@import "params/mix.less";
@import "params/var.less";
