.features{
    padding: 80px 0 120px 0;
    background: #efefef;
    .heart{
        max-width: 230px;
        margin: auto;
        text-align: center;
        margin-bottom: 50px;
        &::before{
            content: "";
            display: inline-block;
            width: 90px;
            height: 2px;
            background: #d7dadc;
            transform: translate(-15px, -7px);
        }
        &::after{
            content: "";
            display: inline-block;
            width: 90px;
            height: 2px;
            background: #d7dadc;
            transform: translate(15px, -7px);
        }
    }
    .feature{
        display: flex;
        .lg-block({
           margin-bottom: 20px; 
        });
        .sm-block({
            flex-direction: column;
            text-align: center;
            align-items: center;
        });
        .image{
            width: 100px;
            margin-right: 15px;
            .sm-block({
               margin-bottom: 10px;
               margin-right: 0px;
            });
        }
        h5{
            font-size: 17px;
            color: @font1;
            margin-bottom: 20px;
            font-weight: 600;
        }
            p {
            color: @font1;
            font-size: 11px;
            font-weight: 400;
            line-height: 17px;
        }
    }
}