.team{
    padding: 100px 0 150px;
    text-align: center;
    .slider{
        margin-top: 100px;
    }
    .member{
        position: relative;
        cursor: pointer;
        &:hover .overlay{
            opacity: 1;
        }
    }
    .overlay{
        position: absolute;
        transition: 1s;
        top: 0;
        left: 0;
        background: rgba(76,118,255, .95);
        height: 100%;
        padding: 15px 20px;
        color: @white;
        font-family: 'Open Sans';
        opacity: 0;
        h6{
            font-weight: 700;
            font-size: 13px;
            line-height: 18px;
            margin-bottom: 10px;
        }
        p{
            font-size: 13px;
            line-height: 18px;
            .media(display,none,476px);
        }
    }
    .social-link{
        width: 35px;
        height: 35px;
        display: inline-block;
        line-height: 35px;
        background: rgba(0,0,0,.3);
        .fa{
            color: @white;
            font-size: 18px;
        }
    }
    .cosial{
        position: absolute;
        bottom: 5%;
        width: 100%;
        left: 0;
    }
}