header{
    height: 100vh;
    .bg("../img/header_bg.jpg");
    font-family: 'Lato' !important;
    position: relative;
    .navbar{
        display: flex;
        justify-content: space-between;
    }
   
    .header-menu{
        display: flex;
        transition: all 1s;
        .close{
            display: none;
            .md-block({
               right: 0;
               display: block;
               width: 10%;
               height: 30%; 
               position: absolute;
               top: 0;
               background: blue;
               outline: none;
               border: none;
            });
        }
        .md-block({
           transform: translateY(-150%);
        });
    }
    .active_header-menu{
        .md-block({
          z-index: 100;
          justify-content: center;
          width: 100%;
          transform: translateY(0%); 
          position: fixed;
          height: 15%;
          top: 0;
          right: 0;
          background: linear-gradient(#024e7e,#26a1da );
        }); 
    }
    .menu-link{
        color: @white;
        text-decoration: none;
        font-size: 12px;
        
        &:hover{
            &::before{
                opacity: 1;
                width: 100%;
            }
        }
        &::before{
            transition: 0.5s;
            content: "";
            display: block;
            width: 40%;
            height: 2px;
            background: @blue;
            position: relative;
            bottom: 19px;
            opacity: 0;
            .md(display,none);
        }
    }
    .logo{
        font-weight: 900;
        font-size: 20px;
        color: @white;
        text-decoration: none;
        .blue{
            color: @blue;
        }
    }
    .menu-top{
        padding: 20px 0;
        background: rgba(15,22,54,.3);
    }
    .menu-item{
        margin: 0 15px;
        text-transform: uppercase;
        .md-block({
            margin: 20px 20px;
            text-align: center;
        });
    }
    .gamb{
        flex-direction: column;
        width: 30px;
        display: none;
        transition: 0.5s;
        cursor: pointer;
        .md-block({
            display: flex; 
            position: fixed;
            right: 10%;
            top: 5%;
            z-index: 22;
         });
        &:hover span:nth-child(2){
            width: 100%;
        }
        &:hover span:nth-child(1){
            width: 100%;
        }
        &:hover span:nth-child(3){
            width: 100%;
        }
        span:nth-child(1){
            width: 70%;
            height: 2px;
            background: @white;
            display: flex;
            transition: 0.5s;
        } 
        span:nth-child(2){
            width: 60%;
            margin-top: 5px;
            height: 2px;
            background: @white;
            display: flex;
            transition: 0.5s;
        } 
        span:nth-child(3){
            width: 75%;
            height: 2px;
            margin-top: 5px;
            background: @white;
            display: flex;
            transition: 0.5s;
        } 
    }
    .header-titel{
        position: absolute;
        color: @white;
        text-align: center;
        width: 100%;
        top: 40%;
        h1{
            font-size: 62px;
            font-weight: 900;
            margin-bottom: 15px;
            .md(font-size, 50px);
            .media(font-size, 37px, 560px);
        }
        h2{
            font-size: 48px;
            font-weight: 100;
            margin-bottom: 25px;
            .md(font-size, 35px);
            .media(font-size, 27px, 560px);
        }
        p{
            font-size: 18px;
            font-weight: 100;
            margin-bottom: 12%;
            .md(font-size, 15px);
            .media(font-size, 12px, 560px);
            &::before {
                content: "";
                display: inline-block;
                width: 28px;
                height: 2px;
                background: @blue;
                transform: translate(-40px, -5px);
            }
            &::after {
                content: "";
                display: inline-block;
                width: 28px;
                height: 2px;
                background: @blue;
                transform: translate(40px, -5px);
            }
        }
        .blue{
            color: @blue;
        }
        .social-icons{
            .fa{
                font-size: 20px;
                color: @white;
                opacity: .4;
                padding: 0 10px;
                &:hover{
                    opacity: 1;
                }
            }
        }
    }
}